@import "./base";

body {
  margin: 0px;
  padding: 0px;
  background-color: black;
  background-image: url("../src/logohack_white-d25227823442525b2cb35c6a03529315.png");
}

#kanbans {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.kanban-column {
  width: 95%;
  margin: auto;
  margin-top: 10px;
  padding: auto;
  border-radius: 5px;
  max-height: 70vh;
  overflow: auto;
}

.category {
  background-color: hsla(220Â°, 53%, 79%, 0.87);
  padding-bottom: 2%;
  border: 2px solid black;
  border-radius: 10px;
  margin: 10px;
  width: 25%;
  .text {
    padding-left: 2%;
    font-family: "Courier New", Courier, monospace;
    font-weight: bold;
  }

  .add-btn {
    padding-left: 2%;
  }
}

.kanban-content {
  padding-top: 0px;
  max-height: 65%;
  overflow: auto;
  .isiKanban {
    background-color: blanchedalmond;
    margin: 5px;
    min-height: 12vh;
    border-radius: 5px;
    font-family: sans-serif;
    font-size: 70%;
    font-weight: bold;
  }
  .isiKanban:hover {
    background-color: #4285f4;
    transition: 300ms ease-in-out;
    cursor: pointer;
  }
}

.button-x button {
  background-color: transparent;
  border: 0;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  -webkit-appearance: none;
}
.button-x button:hover {
  color: red;
}

.button-x {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.button-up button {
  background-color: transparent;
  border: 0;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  -webkit-appearance: none;
}
.button-up button:hover {
  color: green;
}

.button-up {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.btn {
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: 2px solid #e74c3c;
  border-radius: 0.6em;
  color: #e74c3c;
  cursor: pointer;
  display: flex;
  align-self: center;
  font-size: 90%;
  line-height: 1;
  margin-top: 15px;
  margin-left: 10px;
  padding: 0.2em;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}
.btn:hover,
.btn:focus {
  color: #fff;
  outline: 0;
}
.fourth {
  border-color: transparent;
  color: black;
  background-image: linear-gradient(45deg, #162a74 50%, transparent 50%);
  background-position: 90%;
  background-size: 300%;
  transition: background 300ms ease-in-out;
}
.fourth:hover {
  background-position: 0;
}

.kanban-column::-webkit-scrollbar {
  width: 12px;
  border-radius: 10px;
  background-color: #f5f5f5;
}

.kanban-column::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  background-color: #4285f4;
}

.kanban-column::-webkit-scrollbar-thumb:hover {
  transition: 500ms ease-in-out;
  background-color: rgba(0, 0, 0, 0.8);
}

#navbar {
  ul {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    height: 3.5em;

    li {
      float: left;
      height: auto;
      img {
        height: 100%;
      }
    }

    li a {
      display: block;
      color: white;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
      height: 100%;
    }

    /* Change the link color to #111 (black) on hover */
    li a:hover {
      background-color: #111;
    }
  }
}

.addInput {
  margin-left: 2%;
}

.editForm {
  background-color: rgba(0, 0, 0, 0.8);
}
